import { createTheme } from "@mui/material/styles";

// Tạo theme với hai chế độ sáng/tối
const getDesignTokens = (mode) => ({
  palette: {
    mode, // light hoặc dark
    ...(mode === "light"
      ? {
          // Cấu hình màu cho chế độ sáng
          primary: {
            main: "#1976d2", // Màu chính
          },
          secondary: {
            main: "#dc004e", // Màu phụ
          },
          background: {
            default: "#f5f5f5", // Nền chính
            paper: "#ffffff", // Nền cho card
          },
          text: {
            primary: "#333333", // Màu chữ chính
            secondary: "#555555", // Màu chữ phụ
          },
        }
      : {
          // Cấu hình màu cho chế độ tối
          primary: {
            main: "#90caf9", // Màu chính
          },
          secondary: {
            main: "#f48fb1", // Màu phụ
          },
          background: {
            default: "#121212", // Nền chính
            paper: "#1e1e1e", // Nền cho card
          },
          text: {
            primary: "#ffffff", // Màu chữ chính
            secondary: "#aaaaaa", // Màu chữ phụ
          },
        }),
  },
  typography: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
    fontSize: 12,
    h1: { fontSize: "2.2rem" },
    h2: { fontSize: "2rem" },
    h3: { fontSize: "1.8rem" },
    h4: { fontSize: "1.6rem" },
    h5: { fontSize: "1.4rem" },
    h6: { fontSize: "1.2rem" },
  },
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: "none", // Không in hoa chữ
          borderRadius: 4, // Bo góc nút
        },
      },
    },
    MuiTextField: {
      styleOverrides: {
        root: {
          margin: "10px 0",
        },
      },
    },
    MuiCard: {
      styleOverrides: {
        root: {
          borderRadius: 8,
        },
      },
    },
  },
  shape: {
    borderRadius: 10,
  },
  spacing: 4,
  breakpoints: {
    values: {
      xs: 0,
      sm: 600,
      md: 900,
      lg: 1200,
      xl: 1536,
    },
  },
});

export default function createAppTheme(mode) {
  return createTheme(getDesignTokens(mode));
}
