import React from "react";
import "../styles/globals.css";

export default function Layout({ children }) {
  return (
    <div className="flex h-screen">
      {/* Sidebar */}
      <aside className="w-64 bg-gray-800 text-white flex flex-col">
        <div className="p-4 text-xl font-bold text-9xl">SideNav</div>
        {/* Thêm nội dung Sidebar ở đây */}
        <nav className="flex-1 p-4">
          <ul>
            <li className="mb-2">
              <a href="#" className="hover:text-gray-300">
                Dashboard
              </a>
            </li>
            <li className="mb-2">
              <a href="#" className="hover:text-gray-300">
                Profile
              </a>
            </li>
          </ul>
        </nav>
      </aside>

      {/* Main content */}
      <div className="flex-1 flex flex-col">
        {/* Header */}
        <header className="h-16 bg-blue-500 text-white flex items-center px-4">
          <h1 className="text-lg font-bold">Header</h1>
        </header>

        {/* Content */}
        <main className="flex-1 p-4 bg-gray-100">{children}</main>

        {/* Footer */}
        <footer className="h-16 bg-blue-500 text-white flex items-center justify-center">
          <p>Footer</p>
        </footer>
      </div>
    </div>
  );
}
